import service from "../http"
import { getEcsOptions }from '../common'
export default{
    //
    get_operate_record_list(req){
        return service(getEcsOptions('GET','/ecs/history_record/',req))
    },
    //
    get_detail(req){
        return service(getEcsOptions('GET','/ecs/get_ecs_detail/',req))
    },
}